:root {
    --mainprimary: #fff;

}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
    background-color: #fff;
    background-color: var(--mainprimary);
    color: #242424;
    border: 1px solid #fff;
    border: 1px solid var(--mainprimary);
}

.btn--mainprimary {
    background-color: #fff;
    background-color: var(--mainprimary);
    color: #242424;
    border: 1px solid #fff;
    border: 1px solid var(--mainprimary);
}

.btn--mainline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    border: 1px solid var(--mainprimary);
    transition: all 0.3s ease-out;  
}

.btn--mainmedium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--mainarge {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--mainmedium:hover,
.btn--mainlarge:hover {
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}
